<template>
  <div>
    <product-form
      @save-product="updateProduct"
      :model="model"
      :manufacturers="manufacturers"
      :isEditing="true"
      ></product-form>
      <!-- {{this.model}} -->
  </div>
</template>

<script>
// import ProductForm from '@/components/products/ProductForm.vue';

export default {
  created() {
    //
    const { name = '' } = this.model || {};
    if (!name) {
      this.$store.dispatch('productById', {
        productId: this.$route.params.id,
      });
    }

    if (this.manufacturers.length === 0) {
      this.$store.dispatch('allManufacturers');
    }
  },
  computed: {
    manufacturers() {
      return this.$store.getters.allManufacturers;
    },
    model() {
      const product = this.$store.getters.productById(this.$route.params.id);
      const res = { ...product, manufacturer: { ...product.manufacturer } };
      // 这里返回 product 的拷贝，是为了在修改 product 的拷贝之后，在保存之前不修改本地 Vuex stire 的 product 属性
      return res;
    },
  },
  methods: {
    updateProduct(product) {
      this.$store.dispatch('updateProduct', {
        product,
      });
    },
  },
  components: {
    'product-form': () => import('@/components/products/ProductForm'),
  },
};
</script>
